// Page of quotes to show
import React from 'react';

// PageOne Component
var ContactPage = React.createClass({
	// Render a <Quote> element for each element in the state
	render:function() {
		return (
			<div>
	            <form id='contact-form'>
	                <h5>Send me an email!</h5>
	                <h8>I will get back to you as soon as possible.</h8>
	                <input placeholder="Name" type="text" required={true}/>
	                <input placeholder="Email" required={true}/>
	                <textarea rows="4" cols="50" placeholder="Message" required={true} form='contact-form'></textarea>
	                <button type="submit" value="Submit" className='btn'>Submit</button>
	            </form> 
            </div>
		);
	}
});

export default ContactPage;



